.bi-info-circle:hover{
  fill-opacity: 0.3;
}

h1{
  font-size: calc(7.5rem + 2vmin);
}

@-webkit-keyframes gradient-animation {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

@keyframes gradient-animation {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

.App-header {
  background-image: linear-gradient(to top, #249494 0%, #330867 100%);
  background-size: 180% 180%;
  -webkit-animation: gradient-animation 18s ease infinite;
          animation: gradient-animation 18s ease infinite;
  color: white;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(0.938rem + 2vmin);
}

.App-header-2 {
  background-image: linear-gradient(to top, #180c5f 0%, #8a0000 100%);
  background-size: 180% 180%;
  -webkit-animation: gradient-animation 18s ease infinite;
          animation: gradient-animation 18s ease infinite;
  color: white;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(0.938rem + 2vmin);
}

.App-header-3 {
  background-image: linear-gradient(to top, #581aae 0%, #bd1b7a 100%);
  background-size: 180% 180%;
  -webkit-animation: gradient-animation 18s ease infinite;
          animation: gradient-animation 18s ease infinite;
  color: white;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(0.938rem + 2vmin);
}


.App-body {
  font-size: calc(0.625rem + 2vmin);
  text-align: left;
}

.App-bottom {
  font-size: calc(0.063rem + 1vmin);
  text-align: center;
  position: absolute;
  bottom: 0.625rem
}

a {
  color: #c6f7f9;
}

.transform{
  transition: 0.5s ease-in-out;
}

.transform:hover{
  -webkit-transform: scale(1.03);
          transform: scale(1.03);
}

a {
  text-decoration: none;
}


.fade-in-animation {
    opacity: 1;
    -webkit-animation: fade-in 0.5s ease-in;
            animation: fade-in 0.5s ease-in;
  }
  
  @-webkit-keyframes fade-in {
    0% { opacity: 0 }
    100% { opacity: 1}
  }
  
  @keyframes fade-in {
    0% { opacity: 0 }
    100% { opacity: 1}
  }

.counter{
  font-size: calc(6.25rem + 2vmin);
}

.films-watched-header {
  background-image: linear-gradient(to right, #b8cbb8 0%, #b8cbb8 0%, #b465da 0%, #cf6cc9 33%, #ee609c 66%, #ee609c 100%);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(1.25rem + 2vmin);
  color: white;
}

.films-watched-body {
  font-size: calc(0.813rem + 2vmin);
  text-align: left;
}

.title{
  font-size: 2.5rem;
  line-height: 2;
}

.abed-nadir {
  font-size: 1.3rem;
  opacity: 0.5;
  color: white;
}

.genre-loading {
  opacity: 1;
  -webkit-animation: fade 2s ease-in-out;
          animation: fade 2s ease-in-out;
}

@-webkit-keyframes fade {
  0%,100% { opacity: 0 }
  50% { opacity: 1 }
}

@keyframes fade {
  0%,100% { opacity: 0 }
  50% { opacity: 1 }
}

.map-title {
  font-size: calc(1.563rem + 1vmin);
  color: white;
  padding:0.625rem;
  height: 30%;
}

.map-header {
  padding:5.5rem;
  padding-bottom: 3rem;
}

.map-hack {
  padding-bottom: 4rem;
  background-image: linear-gradient(to right, #249494 0%, #330867 100%);
}

.practice-map {
  position:absolute;
  height: 70%;
  width: 100%;
  background-image: linear-gradient(to right, #249494 0%, #330867 100%);

}

.background {
  background-image: linear-gradient(to right, #249494 0%, #330867 100%);
  height: 100%;
  width: 100%;
}

.top-genres-header {
  background-image: linear-gradient(to right, #b8cbb8 0%, #b8cbb8 0%, #b465da 0%, #833ba0 33%, #6346af 66%, #2f5fb8 100%);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
