.genres-tooltip {
  background: rgba(255, 255, 255, 0.31);
  border-radius: 16px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
  border: 1px solid rgba(255, 255, 255, 0.3);
  font-size: 1rem;
  padding: 0.6rem;
  padding-bottom: 0rem;
}

.pie-chart {
  padding: 2rem;
  padding-bottom: 3.5rem;
}

.top-five {
  display:flex; 
  flex-direction:row;
